import NavLeft from '@/components/home/NavLeft.vue';
import MyHeader from '@/components/home/MyHeader.vue';
import { ref } from 'vue';
export default {
  setup() {
    const listMainCenter = ref([]);
    const iconLoading = ref([]);
    const downPullLoading = ref(false);
    const handleDownPull = function (eles) {
      let ele = eles[0];
      console.log('ele:', eles);
      ele.style.position = 'relative';
      ele.appendChild(iconLoading.value);
      // console.log(typeof(ele.children))
      // ele.children.unshift(iconLoading.value);

      let moveStartPos, moveEndPos, scrollTop, moveY;
      let addMoveY = 0;
      let maxMoveY = 70;
      ele.addEventListener('touchstart', e => {
        let cX = e.targetTouches[0].clientX;
        let cY = e.targetTouches[0].clientY;
        moveStartPos = {
          cX,
          cY
        };
      });
      ele.addEventListener('touchmove', e => {
        scrollTop = ele.scrollTop;
        let cX = e.targetTouches[0].clientX;
        let cY = e.targetTouches[0].clientY;
        moveEndPos = {
          cX,
          cY
        };
        moveY = moveEndPos.cY - moveStartPos.cY;
        moveStartPos = moveEndPos;
        if (scrollTop === 0 && moveY > 0) {
          addMoveY += moveY;
          if (addMoveY >= maxMoveY) {
            addMoveY = maxMoveY;
          }
          Object.assign(ele.style, {
            translate: `0 ${addMoveY}px`,
            transition: 'none'
          });
          !downPullLoading.value ? downPullLoading.value = true : '';
        }
      });
      ele.addEventListener('touchend', () => {
        // 重置样式
        addMoveY = 0;
        Object.assign(ele.style, {
          translate: '0',
          transition: 'translate 0.5s'
        });
        setTimeout(() => {
          downPullLoading.value = false;
        }, 600);
      });
    };
    return {
      listMainCenter,
      downPullLoading,
      iconLoading,
      handleDownPull
    };
  }
};